import React, { useEffect, useState } from "react";
import axios from "axios";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";

import Col from "react-bootstrap/Col";
import Carousel from "react-bootstrap/Carousel";
import "../App.css";

import Cellprotofolio, { MyModal, SkillCell } from "../component/cellprotofolio";
import { Image } from "react-bootstrap";
import Constant from "../Constant";

//
const MyBanner = (props) => {
  const [index, setIndex] = useState(0);

  const handleSelect = (selectIndex, e) => {
    setIndex(selectIndex);
  };

  return (
    <Carousel
      interval={3000}
      slide={true}
      touch={true}
      pauseonhover="false"
      controls={false}
      activeIndex={index}
      onSelect={handleSelect}>
      {props.data.map((res, index) => (
        <Carousel.Item key={`banner${index}`}>
          <img className="d-block w-100 bannerSize" src={`${res.img}`} alt={`${res.title}`} />
          <Carousel.Caption>
            <h3>{`${res.title}`}</h3>
            <p>{`${res.desc}`}</p>
          </Carousel.Caption>
        </Carousel.Item>
      ))}
    </Carousel>
  );
};
const WelcomeContainer = (props) => {
  return (
    <Container fluid>
      <br />

      <Col>
        <text className="CenterText TextTitleWelcome">WELCOME</text>
        <div className=" BorderWelcome" />
      </Col>
      <br />
      <Col>
        <text className="CenterText TextTitleWelcomeDes">
          {
            "Hi,thanks for coming to my website,\n this web is place to show my work,\n I hope you like and maybe we can make connection later."
          }
        </text>
      </Col>
    </Container>
  );
};
const PortoContainer = (props) => {
  const [modalShow, setModalShow] = useState(false);
  const [modalData, setModalData] = useState(null);
  const setShowModal = (item) => {
    setModalData(item);
    console.log(item);
    setTimeout(() => {
      setModalShow(true);
    }, 1000);
  };
  return (
    <Container fluid>
      <text className="CenterText TextSubTitle">{props.title}</text>

      <br />
      <Row style={{ justifyContent: "center" }}>
        {props.data.map((res, index) => (
          <Cellprotofolio
            key={`portp${index}`}
            {...props}
            data={res}
            onShow={setShowModal.bind(this)}
          />
        ))}
      </Row>
      {modalData && (
        <MyModal show={modalShow} data={modalData} onHide={() => setModalShow(false)} />
      )}
    </Container>
  );
};

const AboutDiv = (props) => {
  return (
    <>
      <Container id="about">
        <div className="CenterText">
          <Col sm>
            <Image roundedCircle src={Constant.LINK2 + "web_setting/img/foto_profil.png"} />
          </Col>
        </div>
        <br />
        <Row>
          <Col sm>
            <p>
              HI,My Name is Fajar Mandari, I was born on 11-07-1988, and live in kebon jeruk jakarta
              barat, I was graduated from high school SMK 13 in Jakarta, But I have interest on
              creating game because I like to play games,before I know programing, I learn computer
              programming from books and internet.
            </p>
          </Col>
          <Col sm>
            <p>
              I was working at dhub digital for 3 years and then i work at faims media,present i
              work at DotCom Solution as Mobile Programer until now. I am good using React native
              and ActionScript 3 but also know other programming such as Flutter,
              Unity3d,Php,Html,C#,and Java Android, but I believe I still have interest to learn
              those.
            </p>
          </Col>
        </Row>
        <text className="CenterText TextTitleWelcome">Skill</text>
        <div className=" BorderWelcome" />
        {props.data && (
          <div>
            <SkillCell data={props.data.skill} />
          </div>
        )}
      </Container>
    </>
  );
};

const Footer = (props) => {
  return (
    <div className="ContactContainer">
      <div className="ContactItem">
        <text className="CenterText TextContact">LOCATION</text>
        <br />
        <text className="CenterText TextContactDesc">
          Kebon Jeruk <br />
          Jakarta Barat,11550
        </text>
      </div>
      <br />
      <div className="ContactItem">
        <text className="CenterText TextContact">CONTACT ME</text>
        <br />
        <a href={"mailto:fajardsx@gmail.com"}>
          <text className="CenterText TextContactDesc">fajardsx@gmail.com</text>
        </a>
      </div>
    </div>
  );
};

const Home = () => {
  const [appState, setAppState] = useState({ loading: false, repos: null });
  useEffect(() => {
    setAppState({ loading: true });
    const apiurl = "https://raw.githubusercontent.com/fajardsx/Cfg_Repo/master/cfg_web.json";
    axios.get(apiurl).then((result) => {
      const resultData = result.data;
      setAppState({ loading: false, repos: resultData });
    });
  }, [setAppState]);
  return (
    <>
      {appState.repos && <MyBanner data={appState.repos.Banner} />}
      <div className="App-link Porto-Center">
        <div className="porto-centerpart">
          <WelcomeContainer />
        </div>
      </div>
      <div className="App-link Porto-Center">
        <div className="porto-top ">
          <div className="TextTitleContainer">
            <text className="TextTitlePorto">MY PORTFOLIO</text>
          </div>
          <br />
          {appState.repos && (
            <PortoContainer data={appState.repos.Homemobile} title="Mobile Apps" />
          )}

          <br />
          {appState.repos && <PortoContainer data={appState.repos.Homegame} title="Games" />}

          <br />
          <div className=" mb-2 CenterText">
            <a id="MorePorto" href="/porto">
              <h3>MORE PORTFOLIO</h3>
            </a>
          </div>
          <br />
        </div>
      </div>

      <div className="App-link Porto-Center">
        <div className="porto-centerbottom TextTitle">
          <div className="TextTitleContainer ">
            <text className="TextTitleAbout">ABOUT ME</text>
          </div>
          <br />
          <AboutDiv data={appState.repos} />
        </div>
      </div>
      <div className="App-link Porto-Center">
        <div className="porto-footer">
          <br />
          <Footer />
        </div>
      </div>
    </>
  );
};
export default Home;
